import React from "react"
import { Input } from "./input";

import Directions from "../images/directions.jpg"

interface IDirectionLockViewProps {
    onBack: () => void;
    onDone: () => void;
}

export function DirectionLockView(props: IDirectionLockViewProps): JSX.Element {
    const validateInput = (key: string) => {
        if (key.toUpperCase() == "W") {
            return "↑";
        }

        if (key.toUpperCase() == "S") {
            return "↓";
        }

        if (key.toUpperCase() == "A") {
            return "←";
        }

        if (key.toUpperCase() == "D") {
            return "→";
        }

        if (!+key) {
            return undefined;
        }

        return key;
    }

    return (
        <div className="lock">
            <div className="txt">
                <h1>Trajectory plotting</h1>
                <p>You need to plot the trajectory of the rocket you will use to escape.</p>
                <p>Start by finding the asteroid belt in the night sky. Then try to find Earth. You don't want to head towards Saturn. Jupiter is also not your destination. You just want to get back to the blue planet.</p>
            </div>
            <div>
                <img src={Directions} style={{ margin: "auto", display: "block", maxWidth: "600px" }}></img>
            </div>
            <div className="legend" style={{ textAlign: "center" }}>
                <p><span style={{ backgroundColor: "#ffffff" }}><strong>W</strong> ↑</span> <span style={{ backgroundColor: "#ffffff" }}><strong>A</strong> ←</span> <span style={{ backgroundColor: "#ffffff"}}><strong>S</strong> ↓</span> <span style={{ backgroundColor: "#ffffff" }}><strong>D</strong> →</span></p>
            </div>
            <Input expected="↑→↓←→" onDone={props.onDone} validateInput={validateInput}></Input>
            <div>
                <p>... or you could always <a href="#" onClick={props.onBack}>go back</a> and try again later.</p>
            </div>
        </div>);
}
